.menu {
  // position: relative;
  box-sizing: border-box;
  height: 100vh;
  // padding: $headerTopPadding 0;

  overflow-y: auto;

  backdrop-filter: blur(40px);
  background: rgba(122, 112, 94, 0.25); /* Прозорий фон */

  // background-color: #aaa;
  @include onDesktop {
    display: none;
  }

  &__content {
    @include onMobileAndTablet {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
